<template>
  <div class="home-container blue lighten-4">
    <v-layout row v-if="marquee">
      <v-flex col xs4 offset-xs4>
        <v-card class="white" style="margin-top: 50px; text-align: center">
          <v-card-title>
            <h5 style="margin-left: auto; margin-right: auto">Siaaaaam dot com</h5>
          </v-card-title>
          <span class="subheading">How many A's are in Siaaaaam?</span>
          <v-text-field v-model="guess" class="black--text" style="width: 90%; margin-left: 5%; margin-right: 5%" type="number" name="guess" label="Guess">
          </v-text-field>
          <v-btn @click="onGuess">Guess
          </v-btn>
        </v-card>
      </v-flex>
    </v-layout>
    <div v-else>
      <div class="marquee-container">
        <div class="marquee">SI<span v-for="n in Number(correct)">A</span>M</div>
      </div>
      <div class="chat-container">
        <v-layout row style="overflow-y: scroll; height: calc(100vh - 80px)">
          <v-list>
            <v-list-tile v-for="m in messages">
              <v-list-tile-content>
                <v-list-tile-title>
                  {{ m }}
                </v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-layout>
        <v-layout row style="height: 80px">
          <v-text-field class="black--text" type="text" v-model="message" name="message" label="Enter a message">
          </v-text-field>
          <v-btn @click="onMessage">Submit</v-btn>
        </v-layout>
      </div>
    </div>
  </div>
</template>

<script>
import io from 'socket.io-client'

export default {
  name: 'home',
  data() {
    return {
      marquee: true,
      correct: '-1',
      guess: '',
      messages: [],
      socket: undefined,
      message: ''
    }
  },
  created() {
      this.correct = String(Math.floor((new Date() - new Date(2017, 7, 25 - 1)) / 86400000));
      this.socket = io('http://localhost:5000')
      this.socket.on('bc', this.onBroadcast)
  },
  methods: {
    onGuess() {
      if (this.guess == this.correct) {
        this.marquee = false
      }
    },
    onMessage() {
      this.socket.emit('msg', this.message)
      console.log('emitting')
    },
    onBroadcast(data) {
      this.messages.push(data)
      this.message = ''
    }
  }
}
</script>

<style scoped lang="sass">
.home-container
  height: 100vh

.chat-container
  width: 30vw
  float: right

.marquee-container
  float: left
  width: 70vw
  margin: 0 auto
  white-space: nowrap
  overflow: hidden
  box-sizing: border-box

.marquee-container .marquee
  line-height: 100vh
  font-size: 100vh
  display: inline-block
  padding-left: 100%  /* show the marquee just outside the paragraph */
  animation: marquee 15s linear infinite

//.marquee-container .marquee:hover
  //animation-play-state: paused

/* Make it move */
@keyframes marquee
  0%
    transform: translate(0, 0)
  100%
    transform: translate(-100%, 0)

@media screen and (max-width: 900px)
  .marquee-container 
    width: 100vw
  .chat-container
    width: 0
    display: none

</style>



